	/*whole website */

body{
  margin: 0;
  padding: 0;
  background-color: #e6ddda;/*
  overflow-x: hidden;*/
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

/*text formatting*/
$SansSerif: "Roboto Condensed", sans-serif;

p,b,small,big,h1 {
  white-space:normal;
  text-indent: 0;
}
a { text-decoration: none; }

h1{
  font: 400 50px/1.25 "Roboto Condensed", sans-serif;
  color: #333333;
  margin: 0;
  padding: 0;
}
big{
  font: 400 20px/1.25 "Roboto Condensed", sans-serif;
  color: #333333;
  margin: 0;
  padding: 0;
}
p{
  font: 400 16px/1.5 "Roboto Condensed", sans-serif;
  color: #333333;
  margin: 0;
  padding: 0;
}
b{
  font: 700 16px/1.5 "Roboto Condensed", sans-serif;
  color: #333333;
  margin: 0;
  padding: 0;
}
small{
  font: 300 12px/10 "Roboto Condensed", sans-serif;
  color: #333333;
  margin: 0;
  padding: 0;
  display: block;
  text-align: right;
}

/*body fromatting*/

.home_box{
  width: 100%;
  min-height: 80vh;
  flex-direction: row;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.main_box_menu{
  position: relative;
  width: 100%;
  min-height: 80vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.main_box_gallery{
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.main_box_text{
  position: relative;
  width: 100%;
  min-height: 80vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.background_image{
  position: fixed;
  top: 0;
  left: 0;
  min-height: 100%;
  min-width: 100%;
  z-index: -1;
  opacity: 1;
}

/*header*/

.header_banner{
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  height: 70px;
}
.header_banner .title_and_menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.empty{
  width: 100px;
}
.title{
  background-color: #2c2c2c;
  height: 70px;
  width: 150px;
  padding: 0 5px 0 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
}
.title .outline{
  position: absolute;
  top: 5px;
  right: 10px;
  width: 40px;
  height: 20px;
  border-top: 2px solid white;
  border-right: 2px solid white;
}
.title h1{
  font: 400 23px/1.25 "Roboto Condensed", sans-serif;
  color: #ffffff;
  text-align: center;
  letter-spacing: 2px;
}
.nav_menu{
  flex: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin-right: 60px;
}
.nav_item{
  display: block;
  text-align: center;
  font: 300 18px/1.25 "Roboto Condensed", sans-serif;
  color: #2c2c2c;
  margin: 0;
  padding: 0vw 0.5vw 0 0.5vw;
  transition: 0.3s;
}
.nav_item:hover{
  font: 300 20px/1.25 "Roboto Condensed", sans-serif;
  transition-timing-function: ease-out;
  transition: 0.3s;
}

@media screen and (max-width: 600px){
  .empty{
    display: none;
  }
  .nav_menu{
    justify-content: center;
  }
  .nav_item{
    flex: 50%;
  }
}

/*home page*/
.front_page{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.shadow_box{
  background-color: rgba($color: #000000, $alpha: 0.4);
  height: 200px;
  width: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.outline{
  position: absolute;
  top: 10px;
  right: 10px;
  width: 60px;
  height: 30px;
  border-top: 4px solid white;
  border-right: 4px solid white;
}
.front_text{
  font: 700 50px/1 "Roboto Condensed", sans-serif;
  color: #ffffff;
  text-align: left;
  letter-spacing: 2px;
  position: relative;
  transition: .2s ease-in-out;
}
.front_sub_title{
  padding-top: 15px;
  font: 400 30px/1 "Roboto Condensed", sans-serif;
  color: #ffffff;
  text-align: center;
  letter-spacing: 0px;
  transition: .2s ease-in-out;
}
.front_text:hover{
  color: #db2d0e;
  transition: .2s ease-in-out;
}
.front_sub_title:hover{
  color: #db2d0e;
  transition: .2s ease-in-out;
}


/*main menu*/

.main_menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-evenly;
}
.chapter{
  position: relative;
  flex:25%;
  min-width: 150px;
  max-width: 250px;
  height: 60vh;
  min-height: 290px;
  margin: 10px 5px 10px 5px;

  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  box-shadow: 2px 2px 4px rgba($color: #000000, $alpha: 0.4), 4px 5px 8px rgba($color: #000000, $alpha: 0.2) ;

  
  transition-timing-function: ease-out;
  transition: 0.3s;
}
.chapter_cover{
  height: 100%;

  transition-timing-function: ease-out;
  transition: 0.3s;
}
.chapter_title{
  font: 700 30px/1.25 "Roboto Condensed", sans-serif;
  color: #2c2c2c;
  position: absolute;
  text-align: center;
  width: 100%;
  top: 30px;
  left: 0;
  opacity: 0;
  background-color: rgba($color: #ffffff, $alpha: 0.6);
  
}
.chapter:hover .chapter_cover{
  /*opacity: 0.5;*/
  height: 110%;

  transition-timing-function: ease-out;
  transition: 0.3s;
}
.chapter:hover .chapter_title{
  opacity: 1;
  color: #000000;

}
.chapter_with_text{
  flex:25%;
  min-width: 225px;
  max-width: 250px;
  margin: 10px 5px 10px 5px;
  padding: 0 ;
  height: 60vh;
  min-height: 290px;
  
/*
  box-shadow: 2px 2px 4px rgba($color: #000000, $alpha: 0.2), 4px 5px 8px rgba($color: #000000, $alpha: 0.1) ;
*/
}
.chapter_with_text p{
  text-align: justify;
  font: 700 16px/1.5 "Roboto Condensed", sans-serif;

  position: relative;
  top: 0;
  
  margin: 0 0 16px 0;

  transition-timing-function: ease-in;
  transition: 0.3s;
}
.chapter_with_text p:hover {
  top: -10px;
  color: #000000;
  
  transition-timing-function: ease-out;
  transition: 0.3s;
}

@media screen and (max-width: 710px){
  .main_menu{
  flex-wrap: wrap;
  }
  .main_menu .chapter_with_text{
    flex: 40%;
  }
  .main_menu .chapter{
    flex: 40%;
  }
}



/*grid galery*/

.gallery_box {
  flex: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  padding: 20px 0px 10px 0px;
}
.gallery_box .gallery{
  align-self: center;
  flex: 100%;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
}
.gallery_box .gallery .gallery_line{
  align-self: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.gallery_box .gallery .gallery_line .container{
  width: 25%;
}
.thumbnail{
  width: 100%;
  height: 0;
  padding-top: 35%;
  padding-bottom: 35%;
  overflow: hidden;
  background-color: #ffffff;

  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

}
.thumbnail img{
  width: 100%;
  transition: .5s ease-out;
  position: relative;
  top: 0;
}
.thumbnail:hover img{
  top: -15px;
  opacity: 0.6;
  transition: .3s ease-out;
}

/*text*/
.text_container{
  flex:40%;
  min-width: 500px;
  position: relative;
  margin: 50px 50px 50px 50px;
  padding: 50px;
  background-color: rgba($color: #ffffff, $alpha: 0.9);
  text-align: justify;
}


/*----------------------modal stuff----------------------*/

/*----------------modal gallery----------------*/

/* background */

.modal {
  display: none;
  position: fixed; 
  z-index: 3;
  padding: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba($color: #ffffff, $alpha: 0.9);
  vertical-align: middle;
  flex-direction: row;
  flex-wrap: nowrap;
}
@media screen and (max-width: 670px){
  .modal{
    flex-direction: column;
  }
}

	/* Modal Content (image) */

.modal_content {
  flex: 75%;
  margin: auto;
  display: block;/*
  width: 100%;*/
  max-height: 95%;
  object-fit: scale-down;

  /*to make it harder to download*/
  /* also add draggable="false"; to it in html */
  /* menu has been desactivated with js */
}
	/* Caption of Modal Image */

#Caption { 
  flex: 25%;
  min-width: 200px;
  background: white;
  padding: 70px 20px 10px 20px;
  text-align: justify;
  font: 400 16px/1 "Roboto Condensed", sans-serif;
  color: #2c2c2c;
}
/* Add Animation */
.modal_content, #caption{  
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
} 

@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}

@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}

/* all the control Button */

.close {
  position: fixed;
  top: 10px;
  right: 10px;

  height: 50px;
  width: 50px;
  background-color: #2c2c2c;
  color: #ffffff;
  font-size: 35px;
  padding: 5px;
  font-weight: bold;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

.close:hover,
.close:focus {
  color: #98301b;
  text-decoration: none;
  cursor: pointer;
}

.prev{
  cursor: pointer;
  position: fixed;
  right: 130px;
  top: 10px;

  height: 50px;
  width: 50px;
  padding: 10px;
  background-color: #2c2c2c;
  color: #ffffff;
  font-weight: bold;
  font-size: 20px;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}
.next {
  cursor: pointer;
  position: fixed;
  right: 70px;
  top: 10px;

  height: 50px;
  width: 50px;
  padding: 10px;
  background-color: #2c2c2c;
  color: #ffffff;
  font-weight: bold;
  font-size: 20px;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}
.info {/*hidden for now*/
  display: none;
  cursor: pointer;
  position: fixed;
  bottom: 15vh;
  left: 2vw;
  width: auto;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  font-size: 30px;
  transition: 0.6s ease;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

/* On hover, add a black background color */
.prev:hover,
.next:hover,
.info:hover{
  color: #737373;
}